import React from 'react'

interface IState {
    copyright: string
}

class Copyright extends React.Component<{}, IState> {
    constructor(props: {}) {
        super(props)
        this.state = {
            copyright: "© 2020 Jr. All rights reserved. Designed by SmallCorridor."
        }
    }

    render() {
        return (
            <footer>
                <div className="container">
                    <div className="row justify-content-between">
                        <div className="copyright col-12">
                            {this.state.copyright}
                        </div>
                        <div className="social-media">
                        </div>
                    </div>
                </div>
            </footer>
        )
    }
}

export default Copyright